import { useStaticQuery, graphql } from 'gatsby'

/**
 * Site meta data query
 * @returns {Object}
 */
function useSiteMetaData() {
  const { site } = useStaticQuery(graphql`
    query allSiteMeta {
      site {
        siteMetadata {
          defaultTitle
          defaultDescription
          company
          siteUrl
          gitHubUrl  
          contact {
            name
            email
          }
          navigationLinks {
            name
            uri
          }
        }
      }
    }
  `)

  return site.siteMetadata
}

export default useSiteMetaData
